ks-order-check-section .enable span {
    font-size: 22px;
    font-weight: 700;
    font-family: var(--font-emphasis);
}

ks-order-check-section .input {
    display: flex;
    align-items: flex-end;
    margin-bottom: 10px;
}

ks-order-check-section .input > * {
    margin-bottom: 0px;
}

ks-order-check-section .section {
    position: relative;
    overflow: hidden;
}

ks-order-check-section:not([active]) .section {
    opacity: 0.3;
    pointer-events: none;
}

@media (max-width: 959px) {
    ks-order-check-section:not([active]) .section {
        display: none;
    }
}